<template>
  <div class="historyMessageWrapper">
    <div class="backBtn" @click="goBack">
        <span>返回</span>
        <van-icon name="revoke" size="20px" />
    </div>
    <div class="historyMessageList" v-if="showHistoryMessageList">
      <div class="historyMessageItem" v-for="(item,index) in messageList" :key="index">
        <span class="nickName">{{item.nickName}}:</span>
        <p class="text">
          {{item.message}}
        </p>
      </div>
      <div class="clearHistoryMessage" @click="clearHistoryMessage">清空历史留言</div>
    </div>
    <div class="emptyWapper" v-if="!showHistoryMessageList">
      <van-empty
        class="custom-image"
        description="暂无历史留言喔~"
      />
    </div>
  </div>
</template>

<script>
import { Dialog } from 'vant';
export default {
  name: '',
  data(){
    return{
      messageList:JSON.parse(localStorage.getItem('messageList')) || [],
      showHistoryMessageList:false,  // 展示空状态
    }
  },
  props:[
    'showHistory',
    'clearMessageList'
  ],
  methods:{
    /* 
      返回留言页方法
    */
    goBack(){
      this.$emit('setShowHistory')
    },
    /* 
      清空历史留言方法
    */
    clearHistoryMessage(){
      console.log(this.messageList === null);
      console.log(this.messageList[0] === undefined);
      
      Dialog.confirm({
        title: '温馨提示',
        message: '您的所有留言将会被清空，您确定要这么做吗~',
        confirmButtonColor:"##007c40"
      })
      .then(() => {
        localStorage.removeItem("messageList")
        this.messageList = []
        this.showHistoryMessageList = false
        // 调用父组件方法，清空父组件的数据
        this.$emit('clearMessageList')
      })
      .catch(() => {
        
      });

    }
  },
  watch:{
    /* 
      监听messageList，如果没有数据，显示空状态
    */
    messageList:{
      handler(){
        if(this.messageList[0] === undefined){
          // 显示空状态
          this.showHistoryMessageList = false
        }else{
          this.showHistoryMessageList = true
        }
      },
      // 立即执行
      immediate:true
    }
  }
}
</script>

<style lang="less" scoped>
.historyMessageWrapper{
  width: 100%;
  height: 100vh;
  background-color: #F7F8FA;
  .backBtn{
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 30px;
    padding: 0 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: #b8b8b8;
    color: white;
    margin: 10px 0 ;
    span{
      font-size: 12px;
      margin-right: 2px;
    }
  }
  .historyMessageList{
    clear: both;
    .historyMessageItem{
      margin: 0 auto;
      background-color: white;
      // width: 90%;
      padding: 10px 20px;
      margin: 0 20px;
      margin-bottom: 20px;
      border-radius: 20px;
      .nickName{
        display: block;
        line-height: 30px;
        font-weight: bold;
      }
      .text{
        text-indent:1em;
        margin: 0;
      }
    }
    .clearHistoryMessage{
      margin: 0 auto;
      text-align: center;
      color: #007c40;
      font-size: 14px;
    }
  }
  .emptyWapper{
    clear: both;
    display: flex;
    justify-content: center;
  }
}

</style>
