<template>
<div>
  <div class="treeWrapper" v-if="!showHistory">
    <!-- 滚动通知栏 -->
    <van-notice-bar scrollable :text="text" />
    <div class="treeTitle">N益树洞</div>
    <div>
      
    </div>
    <!-- 历史留言按钮 -->
    <div class="historyMessageWrapper">
      <div class="historyMessage" @click="showHistoryMessage">
        <span>历史</span>
        <van-icon 
        name="chat-o" 
        size="20px" 
        v-show="badge===0"
        />
        <van-icon 
        name="chat-o" 
        size="20px" 
        :badge="badge" 
        v-show="badge!==0"
        />
      </div>
    </div>
    <!-- 留言输入框 -->
    <div class="messageInputWrapper">
      <van-form @submit="onSubmit">
        <van-field
          v-model="nickName"
          name="nickName"
          label="昵称"
          placeholder="昵称"
          :rules="[{ required: true, message: '请填写昵称喔~' }]"
        />
        <br>
        <van-field
          v-model="message"
          name="message"
          rows="4"
          autosize
          label="留言"
          type="textarea"
          maxlength="200"
          placeholder="请与小益分享你的故事吧~"
          show-word-limit
          :rules="[{ required: true, message: '请输入留言喔~' }]"
        ></van-field>
        <van-field name="uploader" label=" ">
          <template #input>
            <van-uploader v-model="uploader" />
          </template>
        </van-field>
        <div style="margin: 16px;">
          <van-button class="subButton" round block native-type="submit">分享留言</van-button>
        </div>
      </van-form>
    </div>
  </div>
  <HistoryMessage 
  @setShowHistory="setShowHistory" 
  @clearMessageList="clearMessageList"
  :showHistory="showHistory" 
  v-if="showHistory"/>
</div>
</template>

<script>
import HistoryMessage from '../HistoryMessage/HistoryMessage.vue'
import { Notify } from 'vant';
export default {
  name: '',
  components:{
    HistoryMessage,  
  },
  data() {
    return {
      text:'友善发言，共同维护良好的网络环境喔~',
      nickName: '',
      message:'',
      uploader: [{ url: 'https://img01.yzcdn.cn/vant/leaf.jpg' }],
      showHistory:false, // 控制是否显示历史留言页面
      messageList:JSON.parse(localStorage.getItem("messageList")) || [], // 留言数组
    };
  },
  computed:{
    // 历史留言数量
    badge:{
      get(){
        return this.messageList.length || 0
      },
      set(val){
        console.log(val);
      }
    }
  },
  methods: {
    /* 
      点击提交方法
    */
    onSubmit() {
      let {nickName,message,messageList} = this
      console.log(messageList);
      /* 
        先从localStorage里面取,
        存到messageList中,
        再见用户新添加的存入messageList中
      */
      let obj = {nickName,message}
      messageList.push(obj)
      localStorage.setItem("messageList",JSON.stringify(messageList))

      // 提示留言成功，并清空输入框
      Notify({ type: 'success', message: '留言成功' });
      this.nickName = ''
      this.message = ''
    },
    /* 
      点击展示历史留言
    */
    showHistoryMessage(){
      // 显示历史留言组件
      this.showHistory = true
    },
    /* 
      设置显示历史留言组件
    */
    setShowHistory(){
      this.showHistory = false
      this.badge = this.messageList.length || 0
    },
    /* 
      清空clearMessageList的数据(子组件调用)
    */
    clearMessageList(){
      this.messageList = []
    }
  },
}
</script>

<style lang="less" scoped>
.treeWrapper{
  height: 100vh;
  background-color: #F7F8FA;
  .treeTitle{
    font-size: 20px;
    text-align: center;
    font-weight: bold;
    line-height: 50px;
    color: #007c40;
  }
  .historyMessageWrapper{

    position: relative;
      .historyMessage{
        float: right;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 30px;
        padding: 0 10px;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        background-color: #b8b8b8;
        color: white;
        margin-bottom: 80px;
        span{
          font-size: 12px;
          margin-right: 2px;
        }
      }
  }
  .messageInputWrapper{
    background: url("./img/线性杨树.svg") no-repeat;
    background-size: 60%;
  }

  .subButton{
    color: white;
    font-weight: 600;
    background-color: #007c40;
  }
}
</style>
