import Vue from 'vue'
import App from './App.vue'

// 按需引入vant中需要的组件
import { Field,Form,Button,Uploader,Icon,ContactCard,NoticeBar,Empty,Notify } from 'vant';
import 'vant/lib/index.css';

// 注册vant组件
Vue.use(Field)
Vue.use(Form)
Vue.use(Button)
Vue.use(Uploader)
Vue.use(Icon)
Vue.use(ContactCard)
Vue.use(NoticeBar);
Vue.use(Empty);
Vue.use(Notify);

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
